"use client"
import { useEffect } from "react"
import { useAuth } from "@/context/auth-context"
import { useRouter } from "next/navigation"
import type { PropsWithChildren } from "react";
import { cn } from "@/app/utils/cn";
import { CircleUserRound } from "lucide-react";

export interface AdminLayoutProps extends PropsWithChildren {
  variant?: "light" | "dark";
}

export function AdminLayout({ children, variant }: AdminLayoutProps) {
  const { user, isAuthenticated } = useAuth()
  const router = useRouter()

  useEffect(() => {
    if (!isAuthenticated || user?.role !== "ADMIN") {
      router.push("/login")
    }
  }, [isAuthenticated, user, router])

  if (!isAuthenticated || user?.role !== "ADMIN") return null

   const isLight = variant === "light";
  return (
    <div
      className={cn(
        "relative min-h-screen overflow-hidden transition-colors duration-500",
        isLight
          ? "bg-gradient-to-br from-slate-100 via-white to-slate-200 text-slate-900"
          : "bg-gradient-to-br from-slate-950 via-slate-900 to-blue-950 text-slate-100"
      )}
    >
      <div className="pointer-events-none absolute inset-0">
        <div
          className={cn(
            "absolute inset-x-0 top-[-40%] h-[520px] blur-3xl",
            isLight
              ? "bg-[radial-gradient(circle_at_top,rgba(37,99,235,0.18),rgba(255,255,255,0))]"
              : "bg-[radial-gradient(circle_at_top,rgba(56,189,248,0.28),rgba(15,23,42,0))]"
          )}
        />
        <div
          className={cn(
            "absolute bottom-[-20%] left-1/2 h-[420px] w-[620px] -translate-x-1/2 blur-3xl",
            isLight
              ? "bg-[radial-gradient(circle,rgba(15,23,42,0.12),rgba(255,255,255,0))]"
              : "bg-[radial-gradient(circle,rgba(99,102,241,0.32),rgba(15,23,42,0))]"
          )}
        />
      </div>

      <div className="relative z-10 flex min-h-screen flex-col">
        <header
          className={cn(
            "border-b backdrop-blur-md transition-colors",
            isLight
              ? "border-white/60 bg-white/75 text-slate-900 shadow-sm"
              : "border-white/10 bg-white/5 text-white/90 shadow-lg shadow-blue-900/40"
          )}
        >
          <div className="relative mx-auto flex max-w-6xl items-center justify-between px-6 py-4">
            <div className="absolute inset-x-0 top-full h-px bg-gradient-to-r from-transparent via-white/30 to-transparent opacity-50" aria-hidden />
            <div className="flex items-center gap-3">
              <span className="inline-flex h-10 w-10 items-center justify-center rounded-full bg-gradient-to-br from-blue-500 via-indigo-500 to-purple-500 text-lg font-bold text-white shadow-lg shadow-indigo-500/40">
                FH
              </span>
              <div>
                <p
                  className={cn(
                    "text-sm uppercase tracking-wide",
                    isLight ? "text-slate-500" : "text-white/70"
                  )}
                >
                  FuelHub OS
                </p>
                <h1
                  className={cn(
                    "text-xl font-semibold",
                    isLight ? "text-slate-900" : "text-white"
                  )}
                >
                  Operations Dashboard
                </h1>
              </div>
            </div>

            <button
              className={cn(
                "inline-flex items-center gap-3 rounded-full border px-4 py-2 text-sm backdrop-blur transition",
                isLight
                  ? "border-white/70 bg-white/90 text-slate-700 shadow shadow-slate-300/40 hover:border-indigo-200 hover:text-indigo-600"
                  : "border-white/20 bg-slate-900/40 text-white/85 shadow-lg shadow-blue-900/30 hover:border-white/40 hover:text-white"
              )}
            >
              <CircleUserRound className="h-5 w-5" />
              <span className="hidden sm:inline-flex">Amina Hart</span>
            </button>
          </div>
        </header>

        <main className="px-6 py-10">
          <div className="mx-auto max-w-6xl space-y-8">{children}</div>
        </main>
      </div>
    </div>
  );

}
