"use client"

import { useEffect, useState } from "react"
import { useAuth } from "@/context/auth-context"
import { useRouter } from "next/navigation"
import axios from "axios"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Alert } from "@/components/ui/alert"
import { AdminLayout } from "@/components/layout/admin-layout"
import SalesChart from "@/components/dashboard/sales-chart"
import StatCard from "@/components/dashboard/stat-card"

interface DashboardStats {
  totalSales: number
  fuelInventory: number
  activePumps: number
  attendants: number
}

export default function DashboardPage() {
  const { isAuthenticated, token, user, logout, register } = useAuth()
  const router = useRouter()
  const [stats, setStats] = useState<DashboardStats>({
    totalSales: 0,
    fuelInventory: 0,
    activePumps: 0,
    attendants: 0,
  })
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")
  const [newUserName, setNewUserName] = useState("")
  const [newUserEmail, setNewUserEmail] = useState("")
  const [newUserPassword, setNewUserPassword] = useState("")
  const [newUserRole, setNewUserRole] = useState("attendant")
  const [registerError, setRegisterError] = useState("")
  const [registerLoading, setRegisterLoading] = useState(false)

  // Fetch dashboard data
  useEffect(() => {
    if (!isAuthenticated) {
      router.push("/login")
      return
    }

    const fetchDashboardData = async () => {
      try {
        const headers = { Authorization: `Bearer ${token}` }

        const [salesRes, fuelRes, pumpRes, attendantRes] = await Promise.all([
          axios.get("http://localhost:5000/api/sales", { headers }),
          axios.get("http://localhost:5000/api/fuel", { headers }),
          axios.get("http://localhost:5000/api/pumps", { headers }),
          axios.get("http://localhost:5000/api/attendants", { headers }),
        ])

        setStats({
          totalSales: salesRes.data?.data?.length || 0,
          fuelInventory:
            fuelRes.data?.data?.reduce(
              (sum: number, f: any) => sum + f.quantity,
              0
            ) || 0,
          activePumps: pumpRes.data?.data?.length || 0,
          attendants: attendantRes.data?.data?.length || 0,
        })
      } catch (err) {
        setError("Failed to fetch dashboard data")
      } finally {
        setLoading(false)
      }
    }

    fetchDashboardData()
  }, [isAuthenticated, router, token])

  // Register user (admin only)
  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault()
    setRegisterError("")
    setRegisterLoading(true)
    try {
      await register(newUserName, newUserEmail, newUserPassword, newUserRole)
      setNewUserName("")
      setNewUserEmail("")
      setNewUserPassword("")
      setNewUserRole("attendant")
      alert("User registered successfully!")
    } catch (err: any) {
      setRegisterError(err.message || "Registration failed")
    } finally {
      setRegisterLoading(false)
    }
  }

  return (
    <AdminLayout>
      <div className="min-h-screen bg-[#0b132b] text-white p-8">
        <h1 className="text-3xl font-bold mb-8">Admin Dashboard</h1>

        {error && <Alert variant="destructive">{error}</Alert>}

        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <StatCard title="Total Sales" value={stats.totalSales} icon="ðŸ’°" dark />
          <StatCard title="Fuel Inventory (L)" value={stats.fuelInventory} icon="â›½" dark />
          <StatCard title="Active Pumps" value={stats.activePumps} icon="ðŸ”Œ" dark />
          <StatCard title="Attendants" value={stats.attendants} icon="ðŸ‘¥" dark />
        </div>

        {/* Sales and Alerts */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-8">
          <Card className="bg-[#1c2541] text-white lg:col-span-2 p-6 rounded-2xl shadow-md">
            <h2 className="text-xl font-semibold mb-4">Sales Overview</h2>
            <SalesChart dark />
          </Card>

          <Card className="bg-[#1c2541] text-white p-6 rounded-2xl shadow-md">
            <h2 className="text-xl font-semibold mb-4">Recent Alerts</h2>
            <div className="space-y-3">
              <Alert variant="success">Low inventory on Super 95</Alert>
              <Alert>Pump 3 maintenance due</Alert>
            </div>
          </Card>
        </div>

        {/* Register new user */}
        {user?.role?.toLowerCase() === "admin" && (
          <Card className="bg-[#1c2541] text-white p-6 rounded-2xl shadow-md mb-8">
            <h2 className="text-xl font-semibold mb-4">Register New User</h2>
            {registerError && <Alert variant="destructive">{registerError}</Alert>}
            <form onSubmit={handleRegister} className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <input
                  type="text"
                  placeholder="Full Name"
                  value={newUserName}
                  onChange={(e) => setNewUserName(e.target.value)}
                  required
                  className="w-full px-4 py-2 rounded-lg bg-[#0b132b] border border-gray-600 focus:outline-none"
                />
                <input
                  type="email"
                  placeholder="Email"
                  value={newUserEmail}
                  onChange={(e) => setNewUserEmail(e.target.value)}
                  required
                  className="w-full px-4 py-2 rounded-lg bg-[#0b132b] border border-gray-600 focus:outline-none"
                />
                <input
                  type="password"
                  placeholder="Password"
                  value={newUserPassword}
                  onChange={(e) => setNewUserPassword(e.target.value)}
                  required
                  className="w-full px-4 py-2 rounded-lg bg-[#0b132b] border border-gray-600 focus:outline-none"
                />
                <select
                  value={newUserRole}
                  onChange={(e) => setNewUserRole(e.target.value)}
                  className="w-full px-4 py-2 rounded-lg bg-[#0b132b] border border-gray-600 focus:outline-none"
                >
                  <option value="attendant">Attendant</option>
                  <option value="supplier">Supplier</option>
                  <option value="admin">Admin</option>
                </select>
              </div>

              <Button
                type="submit"
                disabled={registerLoading}
                className="bg-blue-600 hover:bg-blue-700 text-white rounded-lg px-6 py-2"
              >
                {registerLoading ? "Registering..." : "Register User"}
              </Button>
            </form>
          </Card>
        )}

        <Button
          onClick={logout}
          className="bg-red-600 hover:bg-red-700 text-white rounded-lg px-6 py-2"
        >
          Logout
        </Button>
      </div>
    </AdminLayout>
  )
}
